import React, { Component, Suspense } from 'react';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import $ from 'jquery'
import { Modal, Carousel } from 'react-bootstrap';
import Media from '../../CommonComponents/Media/Media';
import { baseUrl } from '../../../../Assets/AssetsHelper';
const GetRightsModalAside = React.lazy(() => import('./Elements/GetRightsModalAside'));
window.$ = $;
global.jQuery = $
class GetRightsModal extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        index: 0,
        direction: null,
    }
    mainRef = React.createRef();
    thumbsRef = React.createRef();
    componentDidMount() {
        if (this.mainRef.current && this.thumbsRef.current && this.thumbsRef.current.splide) {
            this.mainRef.current.sync(this.thumbsRef.current.splide);
        }
    }

    handleSelect = (selectedIndex, e) => {
        console.log("selectedIndex, e", selectedIndex, e)
        if (e) this.setState({
            index: selectedIndex,
            direction: e.direction
        });
    }
    handleClose = (event) => {
        this.props.rightsModalCallback(false)
    };
    carousalImages = (bulkRightsData, thumb) => {
        return bulkRightsData.map((grItem, grindx) => <SplideSlide key={grindx}>
            <Media mediaType={thumb ? grItem.type : 2} postFile={grItem.postFile} mediaHeight={grItem.mediaHeight} mediaWidth={grItem.mediaWidth} socialNetwork={thumb ? grItem.networkId : ''} textContent={grItem.content} rating={grItem.rating} alt={grItem.name} />
        </SplideSlide>)
    };
    render() {
        //const {show} = this.state
        const { rightsModalToggle, modalAssetData, networkData, bulkRightsData } = this.props
        const isBulkStatus = bulkRightsData && bulkRightsData.length > 0 ? true : false
        var socialNetwork = isBulkStatus ? networkData.filter((network) => network.id == bulkRightsData[0].networkId) : networkData.filter((network) => network.id == modalAssetData.networkId)


        const mainOptions = {
            type: 'fade',
            heightRatio: 1,
            pagination: false,
            arrows: true,
            cover: true,
        };

        const thumbsOptions = {
            rewind: true,
            arrows: false,
            fixedWidth: 80,
            fixedHeight: 80,
            isNavigation: true,
            gap: 5,
            focus: 'center',
            pagination: false,
            cover: true,
            dragMinThreshold: {
                mouse: 4,
                touch: 10,
            },
            breakpoints: {
                640: {
                    fixedWidth: 60,
                    fixedHeight: 60,
                },
            },
        };

        return <Modal size={'full'} show={rightsModalToggle} className="rights_modal border-0 p-0 bg-white" backdrop={false}>
            <Modal.Header className="border-0 p-0 w-100">
                <div className="d-flex justify-content-between align-items-center px-lg-3 px-2 h-46px border-bottom bg-white w-100">
                    <div className="d-flex align-items-center flex-wrap">
                        <h1 className="text-dark font-weight-bold mr-4 fs-6 mb-0">
                            {modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 2 ?
                                <span className="svg-icon svg-icon-1 text-success">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                        <path d="M0,0H24V24H0Z" fill="none" />
                                        <path d="M10.112,23.524h-.006a2.092,2.092,0,0,1-.769-.154A14.925,14.925,0,0,1,0,9.472V5.415A2.041,2.041,0,0,1,1.283,3.5L9.324.159A2.026,2.026,0,0,1,10.114,0h.006a1.984,1.984,0,0,1,.785.162L18.944,3.5a2.034,2.034,0,0,1,1.279,1.913V9.476a14.764,14.764,0,0,1-2.541,8.375,14.8,14.8,0,0,1-6.8,5.52A2.034,2.034,0,0,1,10.112,23.524ZM7.1,10.741A1.1,1.1,0,0,0,6.395,11a1.121,1.121,0,0,0-.391.76,1.1,1.1,0,0,0,.266.811L8.608,14.9a1.118,1.118,0,0,0,.779.319H9.4a1.091,1.091,0,0,0,.772-.319l4.169-4.175a.965.965,0,0,0,.139-.138,1.105,1.105,0,0,0-.847-1.813,1.1,1.1,0,0,0-.832.38l-3.41,3.406L7.808,11A1.1,1.1,0,0,0,7.1,10.741Z" transform="translate(1.888 0.238)" fill="currentColor" />
                                    </svg>
                                </span> :
                                modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 1 ?
                                    <span className="svg-icon svg-icon-1 text-warning">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                            <path d="M0,0H24V24H0Z" fill="none" />
                                            <path d="M-937.889-504.477h-.006a2.082,2.082,0,0,1-.769-.154,14.856,14.856,0,0,1-6.8-5.517A14.833,14.833,0,0,1-948-518.528v-4.058a2.05,2.05,0,0,1,1.283-1.916l8.041-3.34a2.034,2.034,0,0,1,.79-.159,2,2,0,0,1,.791.162l8.039,3.338a2.046,2.046,0,0,1,1.279,1.914v4.062a14.835,14.835,0,0,1-2.541,8.375,14.857,14.857,0,0,1-6.8,5.519A2.027,2.027,0,0,1-937.889-504.477Zm-.046-16.137a1.176,1.176,0,0,0-1.166,1.169v3.866a1.177,1.177,0,0,0,.348.811l2.553,2.559.008,0a1.148,1.148,0,0,0,.812.333h.007a1.16,1.16,0,0,0,.819-.341,1.148,1.148,0,0,0,.341-.822,1.162,1.162,0,0,0-.341-.821l-2.209-2.209v-3.381a1.176,1.176,0,0,0-1.171-1.167Z" transform="translate(950.205 528.238)" fill="currentColor" />
                                        </svg>
                                    </span> : modalAssetData.ugcRights && Object.keys(modalAssetData.ugcRights).length > 0 && modalAssetData.ugcRights.status == 3 ?
                                        <span className="svg-icon svg-icon-1 text-danger">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                <path d="M0,0H24V24H0Z" fill="none" />
                                                <path d="M10.112,23.524h-.006a2.1,2.1,0,0,1-.769-.154A14.925,14.925,0,0,1,0,9.472V5.415A2.041,2.041,0,0,1,1.283,3.5L9.324.159A2.033,2.033,0,0,1,10.114,0h.006a1.99,1.99,0,0,1,.785.162L18.944,3.5a2.034,2.034,0,0,1,1.279,1.913V9.476a14.765,14.765,0,0,1-2.541,8.374,14.8,14.8,0,0,1-6.8,5.52A2.041,2.041,0,0,1,10.112,23.524ZM6.68,7.458a1.07,1.07,0,0,0-.239.029.984.984,0,0,0-.429,1.677L8.72,11.875,6.012,14.583a.986.986,0,0,0,.429,1.679l.008,0a.883.883,0,0,0,.2.023h.029a1,1,0,0,0,.722-.313l2.713-2.713,2.711,2.711a1,1,0,0,0,.958.286.986.986,0,0,0,.43-1.678L11.5,11.875l2.709-2.711a.985.985,0,0,0-.43-1.677,1,1,0,0,0-.958.284l-2.713,2.713L7.4,7.772A.985.985,0,0,0,6.68,7.458Z" transform="translate(2 0.238)" fill="currentColor" />
                                            </svg>
                                        </span> :
                                        <span className="svg-icon svg-icon-1 text-success">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                <path d="M0,0H24V24H0Z" fill="none" />
                                                <path d="M10.112,23.524h-.006a2.092,2.092,0,0,1-.769-.154A14.925,14.925,0,0,1,0,9.472V5.415A2.041,2.041,0,0,1,1.283,3.5L9.324.159A2.026,2.026,0,0,1,10.114,0h.006a1.984,1.984,0,0,1,.785.162L18.944,3.5a2.034,2.034,0,0,1,1.279,1.913V9.476a14.764,14.764,0,0,1-2.541,8.375,14.8,14.8,0,0,1-6.8,5.52A2.034,2.034,0,0,1,10.112,23.524ZM7.1,10.741A1.1,1.1,0,0,0,6.395,11a1.121,1.121,0,0,0-.391.76,1.1,1.1,0,0,0,.266.811L8.608,14.9a1.118,1.118,0,0,0,.779.319H9.4a1.091,1.091,0,0,0,.772-.319l4.169-4.175a.965.965,0,0,0,.139-.138,1.105,1.105,0,0,0-.847-1.813,1.1,1.1,0,0,0-.832.38l-3.41,3.406L7.808,11A1.1,1.1,0,0,0,7.1,10.741Z" transform="translate(1.888 0.238)" fill="currentColor" />
                                            </svg>
                                        </span>
                            }
                            Get Rights
                        </h1>
                    </div>
                    <button type="button" className="btn-close me-2 ms-0 text-light" onClick={this.handleClose}></button>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex align-items-stretch border-0 flex-wrap">
                <div className="rights_modal_inner d-flex flex-column flex-grow-1 p-lg-4 p-xxl-5 position-relative mb-2 mb-lg-0">
                    <div className="rights_modal_img_wrapper pt-3 w-100">

                        {isBulkStatus ?
                            <div className="rounded-1 overflow-hidden">
                                <Splide className="mb-3 rights_thumb" options={thumbsOptions} ref={this.thumbsRef}>
                                    {this.carousalImages(bulkRightsData, false)}
                                </Splide>
                                <Splide options={mainOptions} ref={this.mainRef}>
                                    {this.carousalImages(bulkRightsData, true)}
                                </Splide>
                            </div>
                            :
                            <div className="right_slide_img rounded-1 overflow-hidden">
                                <Media mediaType={modalAssetData.type} postFile={modalAssetData.postFile} mediaHeight={modalAssetData.mediaHeight} mediaWidth={modalAssetData.mediaWidth} socialNetwork={modalAssetData.networkId} textContent={modalAssetData.content} rating={modalAssetData.rating} alt={modalAssetData.name} />
                            </div>
                        }
                    </div>
                </div>
                <Suspense fallback={<></>}>
                    <GetRightsModalAside modalClose={this.handleClose} modalAssetData={modalAssetData} isBulkStatus={isBulkStatus} bulkRightsData={bulkRightsData} />
                </Suspense>
            </Modal.Body>
        </Modal >
    }
}

export default GetRightsModal;